@import url(https://fonts.googleapis.com/css2?family=Mulish:wght@300;400;500;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Asap:wght@400;500;700&display=swap);
.navBanner {
  display: block;
  position: absolute;
  top: 110px;
  left: 50%;
  z-index: 10;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  box-shadow: 0px 0px 19px 5px rgba(0, 0, 0, 0.1);
  border-radius: 16px;
  background-color: var(--gray-100);
  padding: 40px 70px;
}

.navBanner__title {
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  color: var(--gray-500);
  margin-bottom: 24px;
}

.navBanner__grid {
  display: grid;
  grid-template-columns: repeat(3, minmax(320px, 1fr));
  grid-column-gap: 20px;
  -webkit-column-gap: 20px;
          column-gap: 20px;
  grid-row-gap: 43px;
  row-gap: 43px;
}

.navBanner__grid__card {
  display: flex;
}

.navBanner__grid__card img {
  display: block;
  width: 34px;
  height: 34px;
  margin-right: 12px;
}

.navBanner__grid__card h4 {
  font-weight: bold;
  font-size: 14px;
  line-height: 18px;
  color: var(--gray-700);
}

.navBanner__grid__card p {
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  color: var(--gray-500);
}

/* nav desktop menu */
.nav__menu {
  display: inline;
  margin-right: 16px;
}

.nav__link {
  margin: 0 0.5rem;
  background-color: transparent;
  font-weight: 400;
  font-size: 16px;
  line-height: 160%;
  color: var(--gray-100);
  opacity: 0.8;
  text-decoration: none;
}

.nav__link.selected,
.nav__link:hover {
  opacity: 1;
}

/* navbar */
.nav {
  padding: 14px 0;
  background-color: var(--primary);
}

/* nav wrapper */
.nav .wrapper {
  max-width: 1400px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--primary);
}

/* nav logo */
.nav__logo {
  width: 45px;
  height: 32px;
  margin-right: 16px;
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat;
}

/* nav logo light */
.nav__logo--light {
  background-image: url(/artivatic/static/media/logo-light.1f8247c4.svg);
}

/* nav Hamburger */
.nav__hamburger {
  width: 26px;
  height: 16px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: transparent;
  cursor: pointer;
}

.nav__hamburger span {
  width: 100%;
  height: 0.15rem;
  background-color: white;
}

/* nav desktop */
.nav__desktop {
  display: none;
  font-family: Mulish;
  font-weight: 600;
  margin-right: 20px;
}

/* nav auth login */
.nav__auth {
  display: none;
  padding: 18px 20px;
  background: var(--primary-dk);
}

/* nav login button */
.nav__login::before {
  content: "";
  display: block;
  width: 16px;
  height: 16px;
  margin-right: 14px;
  background-image: url(/artivatic/static/media/person-vector.e4adf482.svg);
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat;
}

.desk-nav-container {
  display: none;
}

@media screen and (min-width: 1200px) {
  .nav {
    padding: 0 20px;
    background-image: linear-gradient(
      to right,
      var(--primary) 50%,
      var(--primary-dk) 50% 100%
    );
  }
  .nav__desktop {
    display: inline-block;
  }

  .nav .wrapper {
    padding: 0;
    margin: 0 auto;
  }

  .nav__logo {
    width: 70px;
    height: 46.67px;
  }

  .nav__hamburger {
    display: none;
  }

  .nav__auth {
    display: block;
  }

  .desk-nav-container {
    display: flex;
    align-items: center;
  }
}

