.card {
  position: relative;
  width: 160px;
  background-color: var(--gray-100);
  border-radius: 5px;
  overflow: hidden;
}

.card__img {
  display: block;
  width: 100%;
  height: 84px;
}

.card__info {
  padding: 10px 12px;
  text-align: left;
}

.card__tag {
  display: inline-block;
  margin-bottom: 10px;
  border-radius: 2.2px;
  background-color: var(--gray-300);
  padding: 2px 4px;
  font-size: 8px;
  line-height: 1.2;
  color: var(--gray-500);
  vertical-align: top;
}

.card__title {
  margin-bottom: 6px;
  font-weight: 700;
  font-size: 12px;
  line-height: 15px;
  color: var(--gray-700);
}

.card__text {
  font-weight: 400;
  font-size: 8px;
  line-height: 10px;
  color: var(--gray-500);
}

.card__btn {
  display: flex;
  align-items: center;
  margin-top: 10px;
  margin-bottom: 4px;
  background-color: transparent;
  font-weight: 600;
  font-size: 8px;
  line-height: 7px;
  color: var(--primary);
}

.card__btn::after {
  content: "";
  display: block;
  width: 8px;
  height: 5px;
  margin-left: 4px;
  background-image: url(/artivatic/static/media/green-arrow-right.c1709470.svg);
  background-size: 100%;
  background-repeat: no-repeat;
  background-position: center center;
}

.card__hover {
  display: none;
}

@media screen and (min-width: 992px) {
  .card {
    width: 270px;
    min-height: 100px;
    border-radius: 8px;
  }

  .card__img {
    height: 140px;
  }

  .card__info {
    min-height: 141px;
    padding: 14px 24px;
  }

  .card__tag {
    padding: 4px;
    font-size: 12px;
    line-height: 15px;
  }

  .card__title {
    font-size: 20px;
    line-height: 25px;
  }

  .card__text {
    font-size: 16px;
    line-height: 20px;
  }

  /* card with type hover */
  .card.hover .card__text {
    display: none;
  }

  .card__hover {
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 100%;
    left: 0;
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
      url(/artivatic/static/media/cardIMG.3160da8e.png);
    background-size: cover;
    background-position: center center;
    padding: 24px;
    transition: -webkit-transform 500ms ease-in-out;
    transition: transform 500ms ease-in-out;
    transition: transform 500ms ease-in-out, -webkit-transform 500ms ease-in-out;
  }

  .card__hover__title {
    margin-bottom: 6px;
    font-weight: 700;
    font-size: 20px;
    line-height: 25px;
    color: var(--gray-100);
  }

  .card__hover__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: var(--gray-100);
